body {
  margin: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(78, 78, 241);
  color: #fff;
padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  box-sizing: border-box;
}
.container{
width: 72%;
margin: 10px auto;
}
.text-center{
  text-align: center;
  padding: 10px;
  margin: 20px auto;
}

.listitems{
  background-color: #fff;
  text-align: center;
  color: #46529d;
  width: 100%;
}
.listitems p{
  margin: 0;
  text-align: center;
  padding: 10px 0;
}
.listitems span{
  display: inline-block;
  text-align: center;
  padding: 10px 0;
  font-size: 20px;
  border: 1px solid #ccc;
}

.listitems span.title{
  color: #fff;
  background-color: rgb(68, 214, 233);
}
.listitems span.name,
.listitems span.job,
form input[type=text]{
width: 30%;
}
.listitems span.age,
form input[type=number]{
width: 15%;
}
 form input[type=submit]{
  color: #fff;
  width: 10%;
  cursor: pointer;
  background-color:rgb(68, 214, 233); 
}
.listitems span.action{
width: 10%;
}
.listitems span.icon{
color: rgb(240, 9, 9);
cursor: pointer;
}


form input{
  padding: 10px;
  background-color: #cfcccc;
  border: transparent;
  border: 1px solid #ccc;
}